import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { VideoComponent } from './component/video/video.component';
import { AuthenticationGuard } from './service/authentication/authentication.guard';
import { HomeComponent } from './component/home/home.component';
import { ContenedorChatComponent } from './component/chat/contenedor-chat/contenedor-chat.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'video/:id', component: VideoComponent, canActivate: [ AuthenticationGuard] },
  { path: 'chat/:emisor/:receptor', component: ContenedorChatComponent, canActivate: [ AuthenticationGuard] }
];

@NgModule({
  exports: [ RouterModule ],
  imports: [ RouterModule.forRoot(routes)]
})
export class AppRoutingModule { }
