import { Injectable } from '@angular/core';
import { ContactoAgente, UsuarioEscribiendo } from '../domain/cckall.domain';
import { Conversacion, MensajeChat } from '../domain/chat.domain';
import { SessionService } from './session.service';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import * as api from '../config/config';
import { MensajeNuevoMensajeChat } from '../domain/websocket.domain';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
};

@Injectable({
  providedIn: 'root'
})
export class ChatService {

  private usuario: ContactoAgente;
  private conversaciones: Conversacion[] = [];
  // --- comunicacion entre componentes y servicios ---
  mensajeNuevo$: Subject<MensajeNuevoMensajeChat> = new Subject();
  usuarioEscribiendo$: Subject<UsuarioEscribiendo> = new Subject();

  constructor(
    private sesionService: SessionService,
    private http: HttpClient
  ) { }

  usuarioApp(): ContactoAgente {
    return this.sesionService.obtenerUsuarioApp();
  }

  buscarConversacion(idusuarios: number[]): Observable<Conversacion> {
    const url = api.WEBSERVICE_VIDEOLLAMADAS + '/mensajeria/conversacion';
    const params = new HttpParams()
    .append('usuarioId', idusuarios[0] + '')
    .append('usuarioId', idusuarios[1] + '');
    return this.http.get<Conversacion>(url, { params: params});
  }

  buscarConversacionById(id: number): Observable<Conversacion> {
    const url = api.WEBSERVICE_VIDEOLLAMADAS + `/mensajeria/conversacion/${id}`;
    return this.http.get<Conversacion>(url);
  }

  enviarMensaje(conversacionId: number, mensaje: MensajeChat): Observable<MensajeChat> {
    const url = api.WEBSERVICE_VIDEOLLAMADAS + `/mensajeria/conversacion/${conversacionId}/mensaje`;
    return this.http.post<MensajeChat>(url , mensaje, httpOptions);
  }

  enviarEscribiendo(conversacionId: number, usuarioId: number): Observable<any> {
    const url = api.WEBSERVICE_VIDEOLLAMADAS + `/mensajeria/conversacion/${conversacionId}/escribiendo/${usuarioId}`;
    return this.http.post(url, {});
  }

  nuevoUsuarioEscribiendo(escribiendo: UsuarioEscribiendo) {
    this.usuarioEscribiendo$.next(escribiendo);
  }

  nuevoMensaje(mensaje: MensajeNuevoMensajeChat) {
    this.mensajeNuevo$.next(mensaje);
  }

  getMensajesChat(): Observable<MensajeNuevoMensajeChat> {
    return this.mensajeNuevo$.asObservable();
  }

  getUsuariosEscribiendo(): Observable<UsuarioEscribiendo> {
    return this.usuarioEscribiendo$.asObservable();
  }
}
