import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ChatService } from 'src/app/service/chat.service';
import { UsuarioChat, UsuarioEscribiendo } from 'src/app/domain/cckall.domain';
import { Conversacion, MensajeChat } from 'src/app/domain/chat.domain';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { WebsocketService } from 'src/app/service/websocket.service';
import { ChatComponent } from '../chat/chat.component';

@Component({
  selector: 'app-contenedor-chat',
  templateUrl: './contenedor-chat.component.html',
  styleUrls: ['./contenedor-chat.component.css']
})
export class ContenedorChatComponent implements OnInit, OnDestroy {

  usuario: UsuarioChat;
  conversacion: Conversacion;
  usuariosEscribiendo: UsuarioEscribiendo[] = [];
  conversacion$: Observable<Conversacion>;
  escribiendo$: Observable<UsuarioEscribiendo>;
  mensajes$: Subscription;
  conversacionSubscription$: Subscription;
  escribiendoSubscription$: Subscription;

  estadoEscribiendo = false;
  contadorLetras = 0;

  @ViewChild(ChatComponent) hijo: ChatComponent;

  constructor(
    private chatService: ChatService,
    private rutaActiva: ActivatedRoute,
    private websocketService: WebsocketService
  ) { }

  ngOnInit() {
    this.usuario = this.chatService.usuarioApp().usuarioChat;
    // --- se crea subscripcion a cambios de parametros en ruta activa ---
    this.conversacion$ = this.rutaActiva.paramMap.pipe(
      switchMap( params => {
        const emisor = +params.get('emisor');
        const receptor = +params.get('receptor');
        return this.chatService.buscarConversacion([emisor, receptor]);
      })
    );
    this.conversacionSubscription$ = this.conversacion$.subscribe(
      conversacion => {
        console.log('Se encontro conversacion ' + conversacion.id);
        this.conversacion = conversacion;
        setTimeout(() => this.hijo.scrollBottomContenedorMensajes(), 500 );
    });
    this.mensajes$ = this.chatService.getMensajesChat()
    .subscribe(msg => {
      if (this.conversacion.id === msg.conversacionId) {
        console.log('se actualizara vista mensajes');
        if (this.conversacion.mensajes === null ) {
          this.conversacion.mensajes = [];
        }
      this.conversacion.mensajes.push(msg.mensajeChat);
      }
    });
    this.escribiendoSubscription$ = this.chatService.getUsuariosEscribiendo()
    .subscribe(escribiendo => {
      this.usuariosEscribiendo.push(escribiendo);
    });
  }

  ngOnDestroy() {
    this.mensajes$.unsubscribe();
    this.conversacionSubscription$.unsubscribe();
    this.escribiendoSubscription$.unsubscribe();
  }

  onEnviarMensaje(mensaje: MensajeChat) {
    this.chatService.enviarMensaje(this.conversacion.id, mensaje).subscribe();
  }

  onEscribiendo(texto: string) {
    // TODO: implementar logica que no envie mensajes seguidos
     // this.chatService.enviarEscribiendo(this.conversacion.id, this.usuario.id).subscribe();
  }

}
