import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { UsuarioChat, UsuarioEscribiendo } from 'src/app/domain/cckall.domain';
import { Conversacion, MensajeChat } from 'src/app/domain/chat.domain';
import { Observable, Subscription } from 'rxjs';
import { ChatService } from 'src/app/service/chat.service';
import { SessionService } from 'src/app/service/session.service';

@Component({
  selector: 'app-videollamada-chat',
  templateUrl: './videollamada-chat.component.html',
  styleUrls: ['./videollamada-chat.component.css']
})
export class VideollamadaChatComponent implements OnInit, OnDestroy {

  @Input()
  usuario: UsuarioChat;
  @Input()
  conversacion: Conversacion;
  subscripcionMensajes: Subscription;

  constructor(
    private chatService: ChatService,
    private sesionService: SessionService
  ) { }

  ngOnInit() {
    this.subscripcionMensajes = this.chatService.getMensajesChat()
    .subscribe(msg => {
      if (this.conversacion.id === msg.conversacionId) {
        console.log('se actualizara vista mensajes');
        if (this.conversacion.mensajes === null ) {
          this.conversacion.mensajes = [];
        }
        this.conversacion.mensajes.push(msg.mensajeChat);
      }
    });
  }

  ngOnDestroy() {
    this.subscripcionMensajes.unsubscribe();
  }

  onEnviarMensaje(mensaje: MensajeChat) {
    this.chatService.enviarMensaje(this.conversacion.id, mensaje).subscribe();
  }

  onEscribiendo(texto: string) {
    // TODO: implementar logica que no envie mensajes seguidos
     // this.chatService.enviarEscribiendo(this.conversacion.id, this.usuario.id).subscribe();
  }
}


