import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-barra-mensajes',
  templateUrl: './barra-mensajes.component.html',
  styleUrls: ['./barra-mensajes.component.css']
})
export class BarraMensajesComponent implements OnInit {

  textoMensaje: string;
  btnEnviarDeshabilitado = true;
  @Output()
  enviarMensaje = new EventEmitter<string>();
  @Output()
  escribiendo = new EventEmitter<string>();
  @Output()
  adjuntar = new EventEmitter<string>();
  @Output()
  pregrabados = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {
  }

  onEnviarMensaje() {
    this.enviarMensaje.emit(this.textoMensaje);
    this.textoMensaje = '';
    this.btnEnviarDeshabilitado = true;
  }

  onEscribiendo() {
    this.btnEnviarDeshabilitado = this.textoMensaje.length === 0;
    this.escribiendo.emit(this.textoMensaje);
  }

  onAdjuntar() {
  }

  onPregrabados() {

  }
}
