import { Component, OnInit, OnDestroy, Input, ViewChild, ElementRef, AfterViewInit,
  Output, EventEmitter, OnChanges } from '@angular/core';
import { UsuarioChat, UsuarioEscribiendo } from 'src/app/domain/cckall.domain';
import { Conversacion, MensajeChat } from 'src/app/domain/chat.domain';
import { ChatService } from 'src/app/service/chat.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit, OnDestroy, AfterViewInit {

  @Input()
  usuario: UsuarioChat;
  @Input()
  conversacion: Conversacion;
  @Input()
  usuariosEscribiendo: UsuarioEscribiendo[];
  @Output()
  enviarMensaje = new EventEmitter<MensajeChat>();
  @Output()
  escribiendo = new EventEmitter<string>();
  @ViewChild('scrollMensajes') scroll: ElementRef;
  mensajes: Subscription;

  constructor(
    private chatService: ChatService
  ) { }

  ngOnInit() {
    this.mensajes = this.chatService.getMensajesChat()
    .subscribe(msg => {
      setTimeout(() => this.scrollBottomContenedorMensajes(), 500 );
    });
  }

  ngOnDestroy() {
    this.mensajes.unsubscribe();
  }

  ngAfterViewInit() {
    // --- se usa el evento scroll cuando ya se halla iniciado la vista y el elemnto referido no este undefined
    this.scrollBottomContenedorMensajes();
  }

  scrollBottomContenedorMensajes() {
    if (this.scroll) {
      const heightContainer = this.scroll.nativeElement.scrollHeight;
      this.scroll.nativeElement.scrollTop = heightContainer;
    }
  }

  onEnviarMensaje(texto: string) {
    const mensaje: MensajeChat = {
      emisor: this.usuario,
      fecha: new Date(),
      contenido: texto
    };
    this.enviarMensaje.emit(mensaje);
  }

  onEscribiendo(texto: string) {
    this.escribiendo.emit(texto);
  }
}
